<template>
  <div class="project-template">
    <h1>NodeJs Blog</h1>
    <transition name="image" appear>
      <div class="imgs">
        <Carousel class="carousel-comp" @next="next" @prev="prev">
          <CarouselSlide
            class="carousel-slide-comp"
            v-for="(slide, index) in slides"
            :key="index"
            :index="index"
            :visibleSlide="visibleSlide"
            :direction="direction"
          >
            <img :src="require('@/assets/img/' + slide + '')" alt="" />
          </CarouselSlide>
        </Carousel>
      </div>
    </transition>
    <transition name="btns" appear>
      <div class="btns">
        <a target="blank" href="https://github.com/ChrisStyles99/blog-app-node">See code</a>
      </div>
    </transition>
    <transition name="description" appear>
      <div class="project-description">
        <p>
          This Blog was made using NodeJS as the back-end and for the views the template engine EJS was used, also with MongoDB as the database.
        </p>
      </div>
    </transition>
    <transition name="features" appear>
      <div class="features">
        <ul>
          <h1>Features</h1>
          <li>
            Authentication system, users can login and register new accounts.
          </li>
          <li>
            Users can create blogs, update them, and also delete them.
          </li>
          <li>
            Without an account you can see all the posts.
          </li>
          <li>
            A modern design, and easy to use.
          </li>
          <li>
            Has pagination for the all blogs page.
          </li>
          <li>
            Can search a blog for its name.
          </li>
        </ul>
      </div>
    </transition>
    <transition name="tech" appear>
      <div class="tech">
        <ul>
          <h1>Technologies used</h1>
          <li>
            EJS
            <i class="fab fa-html5" style="color: #e34c26;"></i>
          </li>
          <li>
            NodeJS
            <i class="fab fa-node-js" style="color: #3C873A;"></i>
          </li>
          <li>
            ExpressJS
            <i class="fab fa-js" style="color: #444444;"></i>
          </li>
          <li>
            MongoDB
            <i class="fas fa-database" style="color: #4DB33D;"></i>
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
import Carousel from '@/components/Carousel.vue';
import CarouselSlide from '@/components/CarouselSlide.vue';

export default {
  components: {
    Carousel,
    CarouselSlide,
  },
  data() {
    return {
      slides: [
        'node-blog1.png',
        'node-blog2.png',
        'node-blog3.png',
        'node-blog4.png',
      ],
      visibleSlide: 0,
      direction: 'left',
    };
  },
  computed: {
    slidesLen() {
      return this.slides.length;
    },
  },
  methods: {
    next() {
      if (this.visibleSlide >= this.slidesLen - 1) {
        this.visibleSlide = 0;
      } else {
        this.visibleSlide++;
      }
      this.direction = 'left';
    },
    prev() {
      if (this.visibleSlide <= 0) {
        this.visibleSlide = this.slidesLen - 1;
      } else {
        this.visibleSlide--;
      }
      this.direction = 'right';
    },
  },
};
</script>

<style lang="scss">
.project-template {
  display: grid;
  grid-template-columns: 5% 60% 1fr 5%;
  grid-template-rows: auto minmax(auto, 60vh) auto auto;
  min-height: 100vh;

  h1 {
    grid-column: 2 / 4;
    text-align: center;
  }

  .imgs {
    grid-column: 2 / 3;
    .carousel-comp {
      width: 100%;
      height: 100%;

      .carousel-slide-comp {
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  .btns {
    margin: auto;
    grid-column: 3 / 4;

    a {
      display: block;
      padding: 10px 15px;
      margin-bottom: 10px;
      background-color: $primary-color;
      color: $bg-color;
      text-decoration: none;
      font-size: 1.5rem;
      transition: 0.5s ease all;

      &:last-child {
        background-color: $third-color;
      }

      &:hover {
        background-color: $secondary-color;
      }
    }
  }

  .project-description {
    grid-column: 2 / 4;
    width: 80%;
    margin: 0 auto;

    p {
      font-size: 1.5rem;
    }
  }

  .features {
    grid-column: 2 / 4;
    margin: auto;

    ul {
      list-style-type: square;
      li {
        margin: 10px 0;
        font-size: 1.4rem;
      }
    }
  }

  .tech {
    grid-column: 2 / 4;
    margin: auto;

    ul {
      list-style-type: none;
      padding: 0;
      li {
        margin: 10px 0;
        font-size: 1.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        i {
          font-size: 5rem;
        }
      }
    }
  }

  .image-enter-active {
    animation: toRight 1s;
  }

  .btns-enter-active {
    animation: toLeft 1s;
  }

  .description-enter-active {
    opacity: 0;
    animation: fadeIn 1s;
    animation-delay: 1s;
  }

  .features-enter-active {
    opacity: 0;
    animation: fadeIn 1s;
    animation-delay: 1.5s;
  }

  .tech-enter-active {
    opacity: 0;
    animation: fadeIn 1s;
    animation-delay: 2s;
  }

  @keyframes toRight {
    0% {
      transform: translateX(-50%);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes toLeft {
    0% {
      transform: translateX(50%);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}

@media (max-width: 768px) {
  .project-template {
    .imgs {
      grid-column: 1 / 5;
    }

    .btns {
      grid-column: 2 / 4;
      margin-top: 10px;
    }
  }
}

@media (max-width: 600px) {
  .project-template {
    .imgs {
      grid-column: 1 / 5;

      .carousel-comp {
        height: 75%;
      }
    }

    .btns {
      grid-column: 2 / 4;
      margin-top: 10px;
    }
  } 
}
</style>